"use client"; 

import React, { useEffect } from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Scrollbar from "smooth-scrollbar";
import "./Services.css"
const Services = () => {
  useEffect(() => {
    gsap.registerPlugin(ScrollTrigger);
    const bodyScrollBar = Scrollbar.init(document.body, {
      damping: 0.1,
      delegateTo: document,
    });
    ScrollTrigger.scrollerProxy(".scroller", {
      scrollTop(value?: number) {
        if (typeof value === "number") {
          bodyScrollBar.scrollTop = value;
        }
        return bodyScrollBar.scrollTop ?? 0;
      },
    });

    bodyScrollBar.addListener(ScrollTrigger.update);
    gsap.set(".panel", {
      zIndex: (i, _target, targets) => targets.length - i,
    });

    const images = gsap.utils.toArray<HTMLElement>(".panel:not(.purple)");

    images.forEach((image, i) => {
      gsap
        .timeline({
          scrollTrigger: {
            trigger: "section.black",
            scroller: ".scroller",
            start: () => "top -" + window.innerHeight * (i + 0.5),
            end: () => "+=" + window.innerHeight,
            scrub: true,
            toggleActions: "play none reverse none",
            invalidateOnRefresh: true,
          },
        })
        .to(image, { height: 0 });
    });
    gsap.set(".panel-text", {
      zIndex: (i, _target, targets) => targets.length - i,
    });

    const texts = gsap.utils.toArray<HTMLElement>(".panel-text");

    texts.forEach((text, i) => {
      gsap
        .timeline({
          scrollTrigger: {
            trigger: "section.black",
            scroller: ".scroller",
            start: () => "top -" + window.innerHeight * i,
            end: () => "+=" + window.innerHeight,
            scrub: true,
            toggleActions: "play none reverse none",
            invalidateOnRefresh: true,
          },
        })
        .to(text, { duration: 0.33, opacity: 1, y: "50%" })
        .to(text, { duration: 0.33, opacity: 0, y: "0%" }, 0.66);
    });
    ScrollTrigger.create({
      trigger: "section.black",
      scroller: ".scroller",
      scrub: true,
      markers: true,
      pin: true,
      start: "top top",
      end: () => "+=" + (images.length + 1) * window.innerHeight,
      invalidateOnRefresh: true,
    });
    return () => {
      ScrollTrigger.getAll().forEach((t) => t.kill());
      bodyScrollBar.destroy();
    };
  }, []);

  return (
    
		<div className="scroller">
      
        <section className="orange">
            <div className="text">This is some text inside of a div block.</div>
        </section>
  
        <section className="black">
    
            <div className="text-wrap">          
      <div className="panel-text blue-text">Blue</div> 
      <div className="panel-text red-text">Red</div>   
      <div className="panel purple"></div> 
            </div>
    
        </section>
  
        <section className="blue"></section>
  
    </div>
  );
};

export default Services;
